/* General Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.full-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: #f8f9fa;
}

/* Navigation Bar */
.bottom-nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  background: #007bff;
  padding: 10px 0;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-size: 24px;
}

/* Category Buttons */
.categories {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
}

.category-btn {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
}

/* Item Buttons */
.items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.item-btn {
  padding: 10px;
  background: #ffc107;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
}

/* Cart Items */
.cart-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.cart-item-btn {
  padding: 10px;
  background: #17a2b8;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
}

/* Cost Input */
.cost-input {
  margin: 10px;
  font-size: 18px;
}

.cost-field {
  padding: 5px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
